import selfTaskBox from '@/components/self-task-box.vue';
import selfInput from '@/components/self-input.vue';
import selfButtonRadio from '@/components/self-button-radio.vue';
import selfButton from '@/components/self-button.vue';
import timer from '@/components/timer/index.vue';
import selfUpLoad from '@/components/self-upLoad.vue';

export default {
	data() {
		return {
			detail: {},
			accompanying: [],
			accShow: false,
			idNO: '',
			name: '',
			number: '',
			disinfects: ["A", "B"],
			disinfectIdx: 0,
		}
	},
	components: {
		selfTaskBox,
		selfInput,
		selfButtonRadio,
		selfButton,
		timer,
		selfUpLoad
	},
	onLoad(options) {
		const { detail } = options;
		if(detail) {
			this.detail = JSON.parse(detail);
			console.log('-----', this.detail)
		}
	},
	methods: {
		addHandle() {
			if(!this.idNO) {
				uni.showToast({ title: '请输入物资批次', icon: 'none' });
				return
			}
			if(!this.name) {
				uni.showToast({ title: '请输入物资名称', icon: 'none' });
				return
			}
			if(!this.number) {
				uni.showToast({ title: '请输入物资数量', icon: 'none' });
				return
			}
			this.accompanying.push({idNO: this.idNO, name: this.name, number: this.number, disKind: this.disinfects[this.disinfectIdx]})
			this.idNO = '';
			this.name = '';
			this.number = '';
			this.disinfectIdx = 0
			this.accShow = false
		},
		cancleHandle() {
			this.idNO = '';
			this.name = '';
			this.number = '';
			this.disinfectIdx = 0
			this.accShow = false
		},
		addUserHandle() {
			this.accShow = true
		},
		deleteHandle(e, index) {
			this.accompanying.splice(index, 1);
		},
		okClick() {
			alert('a')
		}
	}
}
